$body-color: #3d3d3d;
$yellow: #FFE200;
$green: #ADD345;
$red: #CE0000;
$bg-gray: #E8E7DF;
$line-gray:#E8E7E0;
$serif-font: 'Roboto Slab';
$container-width: 960px;

body {
  background: #fafafa;
  color: $body-color;
  font-family: sans-serif;
}

h2 {
    font-size: 28px;
    font-weight: 800 !important;
    font-family: $serif-font;
    background: repeating-linear-gradient(to bottom, transparent, transparent 20px, #E8E7DF 20px, #E8E7DF 45px) !important;
    line-height: 45px !important;
    display: inline !important;

    xline-height: 0px !important;
    margin-bottom: 20px !important;
}

h3,
h3 b {
    color: #333 !important;
    font-family: $serif-font;
    font-size: 1.2rem !important;
    font-weight: 800 !important;
    background: repeating-linear-gradient(to bottom, transparent, transparent 14px, #E8E7DF 14px, #E8E7DF 32px);
    line-height: 32px !important;
    display: inline !important;

    line-height: 0px !important;
    margin-bottom: 20px !important;
}

h2:before,
h2:after,
h3:before,
h3:after {
    content: '\A';
    white-space: pre;
}
.block-richtext h2 a,
.block-richtext h3 a {
    text-decoration: none;
    color: inherit;
    font-family: inherit;
}
.block-richtext h2 a:hover,
.block-richtext h3 a:hover {
    text-decoration: none;
    color: inherit;
    font-family: inherit;
}
