    width: 100%;
}

.article-edit-form {
    border: 1px solid red !important;
    display: flex !important;
    flex-direction: column !important;
}

main.container {
    background: white;
    max-width: $container-width;
}
main > .container-inner > .content {
    flex-grow: 1;
    max-width: 720px;
    max-width: 960px;
}

main.container,
main.container > div > .container {
    padding: 0px !important;
}
.container-inner {
    display: flex;
}

.header-text {
	color: #ffffff;
	text-align: center;
 }

.header-text h2,
.header-text h2 > p {
	font-size: 2.5rem !important;
	font-weight: 300 !important;
	font-family: $serif-font !important;
	text-shadow: 0px 3px 10px #000000 !important;
	background: none;
	display: inline !important;
	line-height: 3rem;
 }
.header-text h2,
.header-text h3{
    background: none !important;
}
.container.header-text > p {
	text-shadow: 0px 3px 15px #000000 !important;
	font-size: 1.2rem !important;
	margin-bottom: 0px;
	background: none;
 }

.breadcrumb-new {
    font-size: 0.8em;
    max-width: $container-width;
    margin: 0 auto;
    top: 30px;
    position: relative;
}

.breadcrumb-new a {
    font-weight: 300 !important;
    font-size: inherit !important;
}

#quiz-category-question .h2-wrapper-container {
    display: flex;
    justify-content: center;
    margin-bottom: 50px;
}

#quiz-category-question .h2-wrapper {
    display: inline-block;
    font-size: 28px;
    font-weight: 800 !important;
    font-family: $serif-font;
}

@media screen and (max-width: 720px) {
    main.container.container-inner {
        flex-direction: column;
    }
}

.block-content .block-richtext * {
    margin: 10px 0;
}
