#jurcoach-page {
    width: 100%;
}
#jurcoach-page ::selection {
  background: $yellow;
}
#jurcoach-page ::-moz-selection {
  background: $yellow;
}
#jurcoach-page .container a {
    color: #333 !important;
    font-weight: 700;
    font-size: 14px;
}
#jurcoach-page a:hover {
    text-decoration: none !important;
}
#jurcoach-page h1 {
    font-size: 24px;
    font-weight: 800;
    font-family: $serif-font;
}
#jurcoach-page h2 {
    display: inline !important;
    font-size: 28px;
    font-weight: 800 !important;
    font-family: $serif-font;
}
#jurcoach-page div.h2-centered {
    text-align: center;
    margin-bottom: 20px;
}
#jurcoach-page h3 {
    display: inline-block;
    color: #333 !important;
    font-family: $serif-font;
    font-size: 1.2rem !important;
    font-weight: 800 !important;
    background: none !important;
}
#jurcoach-page h2:before,
#jurcoach-page h2:after,
#jurcoach-page h3:before,
#jurcoach-page h3:after {
    content: none !important;
}
#jurcoach-page h3.highlighted {
    background: repeating-linear-gradient(to bottom, transparent, transparent 14px, $bg-gray 14px, $bg-gray 32px) !important;
    line-height: 32px !important;
    display: inline !important;
    & + p {
        margin-top: 8px;
    }
}
#jurcoach-page h3.highlighted.green {
    background:repeating-linear-gradient(to bottom, transparent, transparent 14px, $green 14px, $green 32px) !important;
}
#jurcoach-page .underlined {
    $border-bottom: 4px solid #fff;
    border-bottom: $border-bottom;
    &.hover {
        border-bottom: 0;
        &:hover{
            border-bottom: $border-bottom;
            &.yellow {
                border-color: $yellow;
            }
            &.green {
                border-color: $green;
            }
        }
    }
    &.yellow {
        border-color: $yellow;
    }
    &.green {
        border-color: $green;
    }
}
#jurcoach-page section {
    display: flex;
    flex-direction: column;
 }
#jurcoach-page .container {
    display: flex;
    flex-direction: column;
    max-width: 1100px !important;
}
#jurcoach-page .carousel {
    padding-top: 40px;
    padding-left: 60px;
    width: 1200px;
}

#jurcoach-page .carousel-inner {
    display: flex;
    overflow: initial;
}

#jurcoach-page .carousel-item {
    margin-right: 50px !important;
    max-width: 240px;
    text-align: left;
}
#jurcoach-page .carousel-item p {
    font-size: 13px;
    max-width: 280px;
}
#jurcoach-page .carousel-navigation {
	text-align: right;
	font-size: .9em;
}
#jurcoach-page .carousel-navigation .slider-pagination {
	margin: 0 10px;
}
#jurcoach-page .carousel-navigation .carousel-prev:hover,
#jurcoach-page .carousel-navigation .carousel-next:hover {
	cursor: pointer;
}
#jurcoach-page .bottom-item {
    text-align: left;
    margin-right: 20px;
    width: 25%;
}
#jurcoach-page .bottom-item:nth-of-type(3) {
    margin-right: 60px;
}
#jurcoach-page .bottom-item p {
    font-size: 14px;
}
.jurcoach-poll {
    display: flex;
    flex-direction: column;
    font-size: 14px;
    line-height: 22px;
    background: #fff;
    box-shadow: 3px 3px 0 #000;
    padding: 30px;
    min-width: 220px;
    max-width: 300px;
}
.jurcoach-poll h3 {
    margin-top: -25px;
}
.jurcoach-poll form {
    text-align: center;
}
.jurcoach-poll form > label {
    display: none;
}
.jurcoach-poll form ul {
    text-align: left;
    list-style: none;
    padding: 0px;
}
.jurcoach-poll form ul li label {
    display: inline-flex;
    align-items: center;
    padding-left: .8em;
    cursor: pointer;
}
.jurcoach-poll form ul li label input[type="radio"] {
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background: #fff;
    border-radius: 100%;
    border: 2px solid #000;
    width: 12px;
    height: 12px;
    position: relative;
    left: -10px;
    cursor: pointer;
}
.jurcoach-poll form ul li label input[type="radio"]:checked {
    border-color: #000;
    box-shadow: inset 0 0 0 2px #fff;
    background-color: #000;
}
.jurcoach-poll form [type="submit"]:not(:disabled)  {
    cursor: pointer;
    background: $yellow;
    border: 0;
    padding: 7px 10px;
    width: 100%;
    font-weight: bold;
    &:hover {
        background: #FFD800;
    }
}
.jurcoach-poll:before {
    color: #fff;
    font-family: $serif-font;
    font-weight: 600;
    text-align: center;
    text-transform: uppercase;
    content: 'Umfrage';
    position: relative;
    top: -40px;
    background: #000;
    width: 80px;
}
.jurcoach-poll .answers {
	margin-top: 10px;
	text-align: left;
}
.jurcoach-poll .answers .answer:not(:last-child) {
	margin-bottom: 10px;
}
.jurcoach-poll .answers .answer .bar {
	width: 100%;
	display: flex;
	margin-bottom: 8px;
}
.jurcoach-poll .answers .answer .bar > div {
	background: $yellow;
	max-height: 22px;
	margin-right: 5px;
}
.jurcoach-poll .answers .answer .bar > span {
	float: right;
	font-weight: bold;
	min-width: 70px;
	white-space: nowrap;
}
