	flex-direction: column;
	justify-content: center;
	max-width: 500px;
	min-height: 120px;
	margin: 20px auto 30px auto;
	padding: 20px 100px 20px 20px;
}
#jurcoach-page #article-container {
	margin-bottom: 20px;
}
#jurcoach-page .wiki-article-title {
	margin-bottom: 30px;
	background: repeating-linear-gradient(to bottom, transparent, transparent 20px, #E8E7DF 20px, #E8E7DF 40px);
	line-height: 40px !important;
	display: inline !important;
}
#jurcoach-page .wiki-article-title:after {
	background: transparent !important;
	content: none !important;
}
#jurcoach-page .wiki-article > h2 {
	font-size: 1.3rem !important;
	margin-top: 15px;
}
#jurcoach-page .wiki-article h2:after {
	background: transparent !important;
	content: none;
}
#jurcoach-page .wiki-article .article-edit-title-link {
    margin-left: 10px;
}
#jurcoach-page .line {
    height: 20px;
    border-bottom: 3px solid;
    border-color: $line-gray;
    z-index: -999;
    position: relative;
    top: 70px;
    margin-top: -20px;
    max-width: 1000px !important;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    left: 100px;
}
#jurcoach-page .triangle {
    background: #fff;
    border: 3px solid;
    border-color: $line-gray;
    border-width: 3px 3px 0 0;
    transform: rotate(-45deg);
    position: relative;
    left: 45%;
    top: 8px;
    width: 20px;
    height: 20px;
}

@media (min-width: 768px) {
    /* show 3 items */
    .carousel-inner .active,
    .carousel-inner .active + .carousel-item,
    .carousel-inner .active + .carousel-item + .carousel-item {
        display: block;
    }

    .carousel-inner .carousel-item.active:not(.carousel-item-right):not(.carousel-item-left),
    .carousel-inner .carousel-item.active:not(.carousel-item-right):not(.carousel-item-left) + .carousel-item,
    .carousel-inner .carousel-item.active:not(.carousel-item-right):not(.carousel-item-left) + .carousel-item + .carousel-item {
        transition: none;
    }

    .carousel-inner .carousel-item-next,
    .carousel-inner .carousel-item-prev {
      position: relative;
      transform: translate3d(0, 0, 0);
    }

    .carousel-inner .active.carousel-item + .carousel-item + .carousel-item + .carousel-item {
        position: absolute;
        top: 0;
        right: -33.3333%;
        z-index: -1;
        display: block;
        visibility: visible;
    }

    /* left or forward direction */
    .active.carousel-item-left + .carousel-item-next.carousel-item-left,
    .carousel-item-next.carousel-item-left + .carousel-item,
    .carousel-item-next.carousel-item-left + .carousel-item + .carousel-item,
    .carousel-item-next.carousel-item-left + .carousel-item + .carousel-item + .carousel-item {
        position: relative;
        transform: translate3d(-100%, 0, 0);
        visibility: visible;
    }

    /* farthest right hidden item must be abso position for animations */
    .carousel-inner .carousel-item-prev.carousel-item-right {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        display: block;
        visibility: visible;
    }

    /* right or prev direction */
    .active.carousel-item-right + .carousel-item-prev.carousel-item-right,
    .carousel-item-prev.carousel-item-right + .carousel-item,
    .carousel-item-prev.carousel-item-right + .carousel-item + .carousel-item,
    .carousel-item-prev.carousel-item-right + .carousel-item + .carousel-item + .carousel-item {
        position: relative;
        transform: translate3d(100%, 0, 0);
        visibility: visible;
        display: block;
        visibility: visible;
    }

}
