































































































































































































































































































































































































































































































































































@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.5.0/font/bootstrap-icons.css");
@import url("https://unpkg.com/swiper/swiper-bundle.min.css");

.gamemode {
  overflow: hidden; 
  width: 270px;
  margin: 0 auto;
}

.swiper-controls {
  width: 12rem;
  height: 2.1rem;
  margin: 1rem auto;
  display: flex;
  justify-content: space-between;
}

.swiper-controls .swiper-button-next,
.swiper-controls .swiper-button-prev {
  position: relative;
  margin-top: 0;
  inset: initial;
}

.swiper-container {
  width: 18rem;
  height: 25rem;
}

.swiper-slide {
  perspective: 1000px;
}

.swiper-flashcard {
  background: #fff;
  border-radius: 1em;
  width: 16rem;
  height: 24rem;
  transition: 0.5s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.flashcard-back {
  transform: rotateY(180deg);
}

.swiper-slide-rotate {
  transform: rotateY(180deg);
}

.flashcard-front,
.flashcard-back {
  border-radius: 1em;
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: small;
}

.flashcard-front {
  background: #e9e9e9;
}

.flashcard-back {
  background: #cfe6d4;
}

.flashcards {
  display: flex;
  flex-wrap: wrap;
}

.card {
  margin: 5px;
  padding: 4px;
  background-color: #fff;
  color: #2c3e50;
  height: fit-content;
  border: none;
}

.flip-card {
  border-radius: 1em;
  background-color: transparent;
  width: 150px;
  height: 220px;
  perspective: 1000px;
}

.flip-card-inner {
  border-radius: 1em;
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
  padding: 0px 10px 0px 10px;
  border-radius: 1em;
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flip-card-front {
  background-color: rgb(233, 233, 233);
  color: black;
}

.flip-card-back {
  background-color: rgb(233, 233, 233);
  color: black;
  transform: rotateY(180deg);
}

i:hover {
  cursor: pointer;
}

.tooltips {
  position: relative;
  display: inline-block;
}

.tooltips .tooltiptexts {
  visibility: hidden;
  width: 80px;
  top: 100%;
  left: 50%;
  margin-left: -60px; /* Use half of the width (120/2 = 60), to center the tooltip */
  background-color: rgba(0, 0, 0, 0.639);
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  position: absolute;
  z-index: 1;
}

.tooltips:hover .tooltiptexts {
  visibility: visible;
}

.sidemenu {
  position: relative;
  float: right;
}

.cards-container {
  display: inline-flex;
  width: 850px;
}

.swiper-button-next:after,
.swiper-button-prev:after {
  color: #6c757d;
  font-size: 28px;
}

.card-content {
  margin: 20px;
  text-align: center;
}

.hide {
  display: none;
}

